@import url(https://fonts.googleapis.com/css?family=Allura&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F3F2F2 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.common-app {
    color: #515C6F;
    font-family: 'Playfair Display', serif;
}
.portfolio-page {
    width: 80%;
    margin: 100px 10%;
}
.primary-button-display {
    border-radius: 19px !important;
    background-color: #FF6969 !important;
    border-color: #FF6969 !important;
    box-shadow: 2px 2px 8px #FF6969;
    color: white;
    height: 40px;
    font-family: 'Roboto', serif;
}

@media all and (min-width: 0px) and (max-width: 500px) {
    .portfolio-page {
        width: 80%;
        margin: 80px 10%;
    }
}
.front-page {
    font-size: calc(16px + 1vw);
    width: inherit;
}

.background-with-aga {
    width: 100%;
    height: 100vh;
    background: url(/static/media/aga-rocks.1f1365ca.jpg) no-repeat center center;
    background-size: cover;
    margin: unset;
    opacity: 0.9;
    position: relative;
    color: white;
}
.picture-title {
    position: absolute;
    padding-top: 40vh;
    padding-left: 5vw;
    font-size: calc(18px + 3vw);
}
.picture-extra-information {
    position: absolute;
    padding-top: 80vh;
    padding-left: 5vw;
    font-size: calc(16px + 1vw);
}
.portfolio-title {
    text-align: center;
    padding-top: 5%;
}
.portfolio-item {
    margin-left: 10%;
    width: 80%;
    height: 80%;
    box-shadow: 1px 1px 3px #c2c1c1;
}
.portfolio-cover {
    margin: 2.5% 10%;
    width: inherit;
    position: relative;
}
.portfolio-image {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    -webkit-transition: .5s ease;
    transition: .5s ease;
}
.hovered-filter {
    margin-left: 10%;
    width: 80%;
    height: 100%;
    top: 0;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    background-color: #FF6969;
}
.portfolio-cover:hover .portfolio-image {
    opacity: 0.7;
}
.portfolio-cover:hover .hovered-filter {
    opacity: 0.7;
}
.portfolio-text {
    position: absolute;
    margin-top: 10%;
    margin-left: 50%;
    margin-right: 5%;
    font-size: 5vw;
    color: white;
    text-align: right;
}
.portfolio-details {
    font-size: 2.4vw;
}

@media all and (min-width: 0px) and (max-width: 500px) {
    .portfolio-title {
        padding-top: 15%;
    }

    .portfolio-image {
        opacity: 1;
    }

    .portfolio-text {
        background-color: #FF6969;
        opacity: 0.7;
        padding-right: 5%
    }

    .portfolio-details {
        margin-bottom: 0;
    }

    .hovered-filter {
        opacity: 1;
        background-color: transparent;
    }
}
.about-me-picture {
    width: 80%;
}
.about-me-title {
    font-size: calc(18px + 3vw);
    text-align: center;
}
.about-me-text {
    font-size: calc(12px + 0.6vw);
    margin: 40px 10%;
    text-align: left;
}
.about-me-text p {
    margin-bottom: 30px;
}

@media all and (min-width: 0px) and (max-width: 500px) {
    .about-me-picture {
        width: 100%;
    }

    .about-me-text {
        margin: 40px 0;
    }
}
.project-title {
    text-align: center;
    padding: 0 10%;
}
.project-title h1 {
    padding-bottom: 20px;
}
.project-title-image {
    display: block;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 1px 1px 3px #c2c1c1;
}
.project-section {
    margin: 60px 0;
}
.project-subsection {
    margin: 20px 0;
}
.project-single-picture {
    display: table-caption;
    width: 80vw;
    margin-bottom: 10px;
}
.picture-with-description {
    display: table-caption;
    margin: 10px;
}
.image-shading {
    display: block;
    box-shadow: 1px 1px 3px #c2c1c1;
    margin-left: auto;
    margin-right: auto;
}
.single-picture-normal {
    width: 35%;
}
.single-picture-xlrg {
    width: 65%;
}
.single-picture-fullsize {
    width: 100%;
}
.image-description {
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.multi-picture-spacing {
    padding: 10px;
}

.skip-button {
    text-align: center;
    margin: 20px 0 60px;
}

@media all and (min-width: 0px) and (max-width: 500px) {
    .project-title {
        padding: 0;
    }
    .project-title h3 {
        font-size: 1.4rem;
    }
    .single-picture-normal {
        width: 60%;
    }
    .single-picture-xlrg {
        width: 85%;
    }
}
.resume-title {
    margin-top: 5%;
    margin-bottom: 2.3%;
    font-size: calc(18px + 1vw);
}
.resume-text {
    margin-bottom: 50px;
    font-size: calc(14px + 0.4vw);
}
.resume-footer {
    margin-left: 0 !important;
    padding-left: 0 !important;
}
.resume-footer a {
    color: #515C6F;
    padding-right: 100px;
}

@media all and (min-width: 0px) and (max-width: 500px) {
    .resume-footer-spacing {
        padding-bottom: 20px;
    }
}
.contact-page {
    width: 100%;
    padding: 100px 15% 60px;
}
.contact-page h2 {
    padding-bottom: 30px;
}
.contact-page-container {
    margin: 0 !important;
}
.contact-form {
    max-width: 500px !important;
    min-width: 500px !important;
}
.details-contact-form {
    max-width: 500px !important;
    min-width: 240px !important;
}
.form-group-display {
    background-color: white;
    box-shadow: 1px 1px 3px #c2c1c1;
    border-radius: 10px;
}
.icon-display {
    padding: 25px !important;
}
.no-icon-display {
    padding: 39px !important;
}
.form-display {
    padding-left: 0 !important;
}
.label-display {
    margin-top: 8px !important;
    margin-bottom: 0 !important;
}
.input-display {
    border-color: white !important;
    padding: 0 !important;
    margin-bottom: 2px !important;
    width: 400px !important;
    font-size: calc(12px + 0.2vw) !important;
    min-height: 40px;
}
.contact-me-button {
    box-shadow: 2px 2px 8px #FF6969;
    border-radius: 19px !important;
    background-color: #FF6969 !important;
    border-color: #FF6969 !important;
    min-width: 110px;
    width: 12vw;
    font-family: 'Roboto';
}
.details-title h3 {
    padding-bottom: 5px;
}
.details-content {
    padding-bottom: 30px;
    text-align: right;
}
.toast-display {
    width: 30vw;
}
.alert-fixed {
    position:fixed;
    top: 65px;
    left: 0;
    width: 100%;
    z-index:9999;
    border-radius:0
}

@media all and (min-width: 0px) and (max-width: 500px) {
    .contact-form {
        max-width: 70vw !important;
        min-width: 70vw !important;
    }
    .input-display {
        width: 45vw !important;
    }
}

@media all and (min-width: 0px) and (max-width: 680px) {
    .contact-form {
        max-width: 70vw !important;
        min-width: 70vw !important;
    }
    .input-display {
        width: 45vw !important;
    }
}

.navbar-name {
    vertical-align: middle;
    font-family: 'Allura', cursive;
    font-size: 15pt;
    letter-spacing: 0.1em;
    padding-left: 30px;
}
.navbar {
    background-color: #F3F2F2;
    border-bottom: 1px solid #D7D0D0;
}
.navbar a {
    color: #515C6F;
    font-size: 20px;
    margin-right: 50px;
}
.sub-navbar {
    background-color: #F6F6F6 !important;
    font-size: 10px !important;
    width: 170px;
}
.sub-navbar a {
    font-size: 18px !important;
}

